import { Box, Button, Container } from "@material-ui/core";
import React, { useEffect } from "react";
import { useLocation } from "react-router-dom";
import { Images } from "../../../../core/images/images";
import theme from "../../../../core/theme/theme";

const AppNavBar = () => {
	const location = useLocation();

	const [hasScrolled, setScrolled] = React.useState(false);
	const [alpha, setAlpha] = React.useState(0);

	useEffect(() => {
		window.addEventListener("scroll", calcAlpha);
		return () => {
			window.removeEventListener("scroll", calcAlpha);
		};
	});

	function manageScroll() {
		if (window.scrollY > window.screen.height / 2) {
			setScrolled(true);
		} else {
			setScrolled(false);
		}
	}

	function calcAlpha() {
		const pos = window.scrollY;
		const height = window.screen.height;

		if (pos < height / 2) setAlpha(0);
		else if (pos > height) setAlpha(1);
		else setAlpha(Math.abs(window.scrollY / (height / 2) - 0.5));
		console.log(alpha);
	}

	return (
		<Box
			position="fixed"
			zIndex={theme.zIndex.appBar}
			width="100%"
			bgcolor={`rgba(255, 255, 255, ${alpha})`}
			boxShadow={alpha >= 1 ? theme.shadows[2] : "none"}
			py={{ xs: 2, md: 4 }}
		>
			<Container>
				<Box display="flex" alignItems="center" justifyContent="space-between">
					<Box height={20}>
						<img src={Images.logo} alt="sert-nation-logo" />
					</Box>
					{/* <Box display="flex">
						<AppNavBarItem title="Affiliate" />
						<AppNavBarItem title="About Us" />
					</Box> */}
				</Box>
			</Container>
		</Box>
	);
};

const AppNavBarItem = ({ title }: { title: string }) => {
	return <Button style={{ fontWeight: 700 }}>{title}</Button>;
};

export default AppNavBar;
