import { configure } from "mobx";
import React from "react";
import { inject } from "../../injection-container";

configure({ enforceActions: "always" });

//initialize bottle container
inject();

//Defining the store dataType
type StoreType = {};

//Create a storeContext
const StoreContext = React.createContext<StoreType>({} as StoreType);

//Store provider component
export const StoreProvider: React.FC<React.PropsWithChildren<{}>> = ({ children }) => {
	return <StoreContext.Provider value={{}}>{children}</StoreContext.Provider>;
};

//Hook to use StoreContext
export const useStoreContext = () => React.useContext(StoreContext);
