import { Box, Button, colors, Theme, Typography, useMediaQuery } from "@material-ui/core";
import { Copyright, FavoriteOutlined } from "@material-ui/icons";
import React from "react";
import theme from "../../../../../core/theme/theme";
import AllCenterBox from "../../general/all-center-box";

const FooterCopyrightCmp = () => {
	const isXs = useMediaQuery((theme: Theme) => theme.breakpoints.down("xs"));

	return (
		<>
			<Box
				display="flex"
				justifyContent="space-between"
				alignItems="center"
				flexDirection={isXs ? "column" : "row"}
				px={{ xs: 1, md: 2 }}
			>
				<Box display="flex" alignItems="center">
					<Copyright fontSize="small" style={{ color: colors.grey[500] }} />
					<Box px={0.5} />
					<Typography variant="button" style={{ color: colors.grey[500] }}>
						{getYear()} sertnation.com, all rights reserved
					</Typography>
				</Box>
				<Box borderBottom={`1px solid ${theme.palette.primary.main}`}>
					<Button style={{ color: colors.grey[500], letterSpacing: 1 }} onClick={scrollToTop}>
						scroll to top
					</Button>
				</Box>
			</Box>
			<AllCenterBox flexDirection="row" pt={{ xs: 4 }}>
				<Typography variant="body2" style={{ color: colors.grey[500], textAlign: "center" }}>
					Made with
				</Typography>
				<FavoriteOutlined
					fontSize="small"
					style={{ color: theme.palette.primary.main, margin: "0 5px" }}
				/>
				<Typography variant="body2" style={{ color: colors.grey[500], textAlign: "center" }}>
					by developers.
				</Typography>
			</AllCenterBox>
			<AllCenterBox flexDirection="row" pt={{ xs: 4 }}>
				<Typography variant="caption" style={{ color: colors.grey[600], textAlign: "center" }}>
					* Terms and Conditions Apply
				</Typography>
			</AllCenterBox>
		</>
	);

	function scrollToTop(): void {
		window.scrollTo(0, 0);
	}

	function getYear(): number {
		const date = new Date();
		return date.getFullYear();
	}
};

export default FooterCopyrightCmp;
