import { Box, colors, Container, IconButton, makeStyles, Typography } from "@material-ui/core";
import { KeyboardArrowLeft, KeyboardArrowRight } from "@material-ui/icons";
import * as React from "react";
import { Images } from "../../../../core/images/images";
import { homeViewStrings } from "../../../../core/strings/home-view-strings";
import theme from "../../../../core/theme/theme";

const images = [Images.home1, Images.home2, Images.home3];

const HomeCasouselCmp = () => {
	const [index, setIndex] = React.useState(0);

	React.useEffect(() => {
		const interval = setInterval(setNextImage, 30 * 1000);
		return () => {
			clearInterval(interval);
		};
	});

	function setNextImage() {
		setIndex((index + 1) % images.length);
	}

	function setPrevImage() {
		setIndex(Math.abs(index - 1) % images.length);
	}

	function setSpecificImage(pos: number) {
		setIndex(pos);
	}

	return (
		<Box position="relative">
			<ImageCmp image={images[index]} />
			<TextCmp
				heading={homeViewStrings.carousel[index].heading}
				content={homeViewStrings.carousel[index].content}
				light={false}
			/>
			<ControlsCmp
				length={images.length}
				setNextImage={setNextImage}
				setPrevImage={setPrevImage}
				setSpecificImage={setSpecificImage}
				selected={index}
			/>
		</Box>
	);
};

const TextCmp = ({
	heading,
	content,
	light,
}: {
	heading: string;
	content: string;
	light: boolean;
}) => {
	const classes = useStyles();

	return (
		<Box
			className={classes.overlay}
			top={0}
			position="absolute"
			height="100vh"
			width="100%"
			display="flex"
			alignItems="center"
			justifyContent="center"
			// bgcolor="#00000045"
		>
			<Container>
				<Typography
					variant="h1"
					style={{ textShadow: "1px 0 1px #00000050", color: "#f5f5f5" }}
					gutterBottom
				>
					{heading}
				</Typography>
				<Typography
					variant="h5"
					component="p"
					style={{ textShadow: "1px 0 1px #00000050", color: "#f5f5f5", letterSpacing: 1 }}
				>
					{content}
				</Typography>
			</Container>
		</Box>
	);
};

const useStyles = makeStyles((theme) => ({
	overlay: {
		background:
			"linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0.4) 30%, rgba(0,0,0,0.4) 70%, rgba(0,0,0,0) 100%)",
	},
}));

const ControlsCmp = ({
	selected,
	length,
	setPrevImage,
	setNextImage,
	setSpecificImage,
}: {
	selected: number;
	length: number;
	setPrevImage: () => void;
	setNextImage: () => void;
	setSpecificImage: (a: number) => void;
}) => {
	return (
		<>
			<Box position="absolute" left={0} top="50%">
				<IconButton onClick={setPrevImage} color="primary">
					<KeyboardArrowLeft />
				</IconButton>
			</Box>
			<Box position="absolute" right={0} top="50%">
				<IconButton onClick={setNextImage} color="primary">
					<KeyboardArrowRight />
				</IconButton>
			</Box>
			<Box position="absolute" bottom={10} display="flex" justifyContent="center" width="100%">
				{renderDots()}
			</Box>
		</>
	);

	function renderDots() {
		let list: any[] = [];

		for (let i = 0; i < length; ++i) {
			list.push(
				<Box
					width={15}
					height={15}
					bgcolor={i === selected ? theme.palette.primary.main : colors.grey[400]}
					mx={1}
					borderRadius={40}
					onClick={() => {
						setSpecificImage(i);
					}}
					style={{ cursor: "pointer" }}
				/>,
			);
		}

		return list;
	}
};

const ImageCmp: React.FC<{ image: string }> = ({ image }) => {
	return (
		<Box height="100vh" width="100%">
			<img src={image} alt="home_carousel_image" style={{ objectFit: "cover" }} />
		</Box>
	);
};

export default HomeCasouselCmp;
